package io.github.shub39.portfolio.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.core.*
import com.varabyte.kobwebx.markdown.*

@Page
@Composable
fun ProjectsPage() {
    CompositionLocalProvider(LocalMarkdownContext provides MarkdownContext("Projects.md", mapOf())) {
        io.github.shub39.portfolio.components.layouts.MarkdownLayout("Projects") {
            org.jetbrains.compose.web.dom.H1(attrs = { id("other-projects") }) {
                org.jetbrains.compose.web.dom.Text("Other Projects")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("Coming Soon...")
            }
        }
    }
}
